const defaultStorage = 'local';

export function getItem(itemKey, storageType = defaultStorage) {
  if (typeof window === 'undefined') {
    return false;
  }

  const storage = getStorage(storageType);
  const item = storage.getItem(itemKey);

  try {
    return JSON.parse(item);
  } catch (e) {
    return item;
  }
}

export function setItem(itemKey, value, storageType = defaultStorage) {
  if (typeof window === 'undefined') {
    return false;
  }

  const storage = getStorage(storageType);

  if (typeof value === 'object') {
    storage.setItem(itemKey, JSON.stringify(value));
  } else {
    storage.setItem(itemKey, value);
  }

  return true;
}

export function removeItem(itemKey, storageType = defaultStorage) {
  if (typeof window === 'undefined') {
    return false;
  }

  const storage = getStorage(storageType);

  try {
    storage.removeItem(itemKey);
    return true;
  } catch (e) {
    return false;
  }
}

function getStorage(storageType) {
  return storageType === 'local' ? localStorage : sessionStorage;
}

export default {
  getItem,
  setItem,
  removeItem,
};



